import React, { FC, useContext } from 'react'
import {
    Box,
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer, TableHead, TableRow,
    Typography,
    Chip,
    Button
} from '@mui/material'
import {useNavigate, useParams} from 'react-router-dom'
import moment from 'moment'

import { AppTreeContext } from '../App'
import { IAppTreeVersion } from '../types/tree'

const Versions: FC = () => {
    const { configId } = useParams()
    const navigate = useNavigate()

    const app = useContext(AppTreeContext)
    const { appTree, appSettings, updateRequests } = app

    const config = Object.values(appTree).filter(g => g.configs[Number(configId)])[0].configs[Number(configId)]
    const versions: IAppTreeVersion[] = config.versions

    const isFresh = (src: string): 'red' | 'black' => {
        return moment().diff(moment(src), 'days') <= 7 ? 'red' : 'black'
    }

    const handleClick = (versionId: number) => {
        const files = config.versions.filter((v: IAppTreeVersion)  => v.versionId === versionId)[0].files
        if (files && files.length) navigate(`/files/${versionId}`)
    }

    const handleRequestClick = (versionId: number) => {
        const currentRequest = [...appSettings.requests]
        if (currentRequest.find(i => i.id === versionId)) {
            currentRequest.forEach(i => {
                if (i.id === versionId) {
                    i.newValue = !i.newValue
                }
            })
        } else {
            currentRequest.push(
                {
                    id: versionId,
                    oldValue: false,
                    newValue: true
                }
            )
        }
        updateRequests(currentRequest)
    }

    const getStatusColor = (status: string, versionId: number): { label: string, color: 'error' | 'warning' | 'success' } => {
        if (appSettings.requests.filter(r => r.id === versionId)[0]?.newValue) return { label: ' ', color: 'error' }
        if (status === 'not available') return { label: '', color: 'error' }
        if (status === 'request') return { label: ' ', color: 'warning' }
        return { label: '', color: 'success' }
    }

    return (
        <Box sx={{ p: 3 }}>
            <Typography variant="h5" gutterBottom>
                 
            </Typography>

            <TableContainer component={Paper} sx={{ maxWidth: 800 }}>
                <Table>
                    <TableHead>
                        <TableRow sx={{ bgcolor: 'primary.light' }}>
                            <TableCell sx={{ color: 'white', fontWeight: 'bold' }}></TableCell>
                            <TableCell sx={{ color: 'white', fontWeight: 'bold' }}></TableCell>
                            <TableCell sx={{ color: 'white', fontWeight: 'bold' }}></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {versions.map((row) => (
                            <TableRow
                                key={row.versionId}
                                hover
                                sx={{
                                    '&:nth-of-type(odd)': { bgcolor: 'action.hover' },
                                    '&:last-child td, &:last-child th': { border: 0 }
                                }}
                                onClick={() => handleClick(row.versionId)}
                            >
                                <TableCell>{row.versionName}</TableCell>
                                <TableCell sx={{ color: isFresh(row.versionDate) }}>{row.versionDate ? moment(row.versionDate).format('DD.MM.YYYY') : '-'}</TableCell>
                                <TableCell>
                                    {[' ', ' '].includes(getStatusColor(row.versionStatus, row.versionId).label)
                                        ?
                                            <Button
                                                variant="outlined"
                                                color={getStatusColor(row.versionStatus, row.versionId).color}
                                                onClick={() => handleRequestClick(row.versionId)}
                                            >
                                                {getStatusColor(row.versionStatus, row.versionId).label}
                                            </Button>
                                        :
                                            <Chip
                                                label={getStatusColor(row.versionStatus, row.versionId).label}
                                                color={getStatusColor(row.versionStatus, row.versionId).color}
                                                size="small"
                                            />
                                    }
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </Box>
    )
}

export default Versions